html,
body {
  margin: 0;
  font-size: 62.5%;
  overflow-x: hidden;
  background-color: #6aa7b2;
}

#root {
  overflow: hidden;
  max-height: 100%;
}

@media (max-width: 980px) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 700px) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 35%;
  }
}

.App {
  position: relative;
}
.heading {
  text-align: center;
  position: absolute;
  top: 5%;
}
.ocean {
  height: 5%;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: #015871;
}

.wave {
  background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/85486/wave.svg)
    repeat-x;
  position: absolute;
  top: -19.8rem;
  width: 640rem;
  height: 19.8rem;
  animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
  transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  top: -17.5rem;
  animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite,
    swell 7s ease -1.25s infinite;
  opacity: 1;
}

@keyframes wave {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: -1600px;
  }
}

@keyframes swell {
  0%,
  100% {
    transform: translate3d(0, -2.5rem, 0);
  }
  50% {
    transform: translate3d(0, 0.5rem, 0);
  }
}

.header-container {
  position: relative;
  width: 100%;
  height: 80rem;
  color: black;
  background: linear-gradient(
    0deg,
    rgb(209, 173, 54) 0%,
    rgb(201, 218, 107) 35%,
    rgb(54, 189, 160) 100%
  );
}

.extra {
  color: rgb(50, 87, 116);
}

.profile {
  display: flex;
  flex-direction: row;
  height: 25rem;
  position: absolute;
  bottom: 10rem;
  width: 100%;
  justify-content: center;
}

.profile img {
  width: 25rem;
  height: 25rem;
  border-radius: 50%;

  animation: bubble 3s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite;
  animation-direction: alternate;
  padding: 0.7rem;
  background-color: azure;
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
    rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
    rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px,
    rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px,
    rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

@keyframes bubble {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(-2rem);
  }
  50% {
    transform: translateY(-4rem);
  }
  100% {
    transform: translateY(-5rem);
  }
}

.myname {
  margin-top: 100px;
  text-align: center;
  font-size: 8rem;
  color: rgb(255, 255, 255);
  font-family: sans-serif;
  font-weight: 500;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: rgb(248, 248, 248);
  text-shadow: 0.5rem 0.5rem 0.4rem #252525;
}

.myname span {
  opacity: 0;
  position: relative;
  display: inline-block;
  margin: 5px;
  z-index: 5;
}

.about-section {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  height: auto;
  padding: 5rem;
  color: aliceblue;
  background: rgb(106, 167, 178);
  background: linear-gradient(
    0deg,
    rgb(9, 115, 145) 0%,
    rgba(106, 167, 178, 1) 35%,
    rgba(1, 88, 113, 1) 100%
  );
}

.heading {
  z-index: 1;
  position: relative;
  font-size: 7rem;
  font-weight: bolder;
  color: aliceblue;
  text-align: center;
  width: 100%;
  margin-bottom: 50px;
}
.helloimg {
  width: 35rem;
  height: 30rem;
  margin: 10px;
}
.helloimg img {
  width: 35rem;
  height: 30rem;
  border-radius: 5px;
}

.about-content {
  width: 80rem;
  height: auto;
  font-family: sans-serif;
  color: white;
  font-size: 3rem;
}

.logo {
  width: 10rem;
  height: 10rem;
  background-color: white;
  border-radius: 50%;
  border: solid 0.2rem black;
}

.education-section {
  padding-top: 15rem;
  padding-bottom: 5rem;
  background: linear-gradient(
    0deg,
    rgb(9, 115, 145) 0%,
    rgba(106, 167, 178, 1) 30%,
    rgb(9, 115, 145) 100%
  );
  position: relative;
}

.skills-section {
  padding: 5rem;
  padding-top: 15rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  background: linear-gradient(
    0deg,
    rgb(9, 115, 145) 0%,
    rgba(106, 167, 178, 1) 30%,
    rgb(9, 115, 145) 100%
  );
  position: relative;
}
.mydate {
  color: aliceblue;
  font-family: sans-serif;
  font-size: 2rem !important;
}

#description {
  font-size: 2rem;
}

.Skill {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  background-color: azure;
  /* background: url(./components/three.png); */
  background-size: cover;
  background-position: center;
  width: 30rem;
  height: 20rem;
  border-radius: 1rem;
  margin: 2rem;
  transition: all 500ms ease-in-out !important;
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
    rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
    rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px,
    rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px,
    rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

.Skill:hover {
  scale: 1.2;
}
.skill-img {
  width: 13rem;
  height: 13rem;
  border-radius: 1rem;
  background: inherit;
}
.progress-bar {
  width: 10rem;
  height: 10rem;
}

.right-skill {
  width: 15rem;
  height: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.skillname {
  color: black;
  text-align: center;
  font-family: sans-serif;
  font-size: 3.5rem;
  width: 100%;
}
.skill-img img {
  width: 13rem;
  height: 13rem;
  border-radius: 1rem;
}

.contactme-section {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 8rem;
  padding-bottom: 15rem;
  justify-content: space-around;
  background-size: cover;
  background-repeat: no-repeat;
  background: linear-gradient(
    0deg,
    rgb(9, 115, 145) 100%,
    rgba(106, 167, 178, 1) 30%,
    rgb(9, 115, 145) 100%
  );
  height: auto;
}

@keyframes opposite {
  0% {
    transform: scale(2);
    border-radius: 50%;
  }
  25% {
    transform: scale(1.7);
    border-radius: 30%;
  }
  50% {
    transform: scale(1.5);
    border-radius: 20%;
  }
  75% {
    transform: scale(1.3);
    border-radius: 10%;
  }
  100% {
    transform: scale(1);
    border-radius: 2rem;
  }
}
.handle-btn {
  width: 7rem;
  height: 7rem;
  background-color: aliceblue;
  z-index: 2;
  border-radius: 2rem;
  animation: opposite 0.3s ease-in-out 1 alternate forwards !important;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
}

@keyframes rotate-box-changeShape {
  0% {
    transform: rotate(0deg) scale(1);
    border-radius: 0%;
  }
  25% {
    transform: rotate(90deg) scale(1.3);
    border-radius: 10%;
  }
  50% {
    transform: rotate(180deg) scale(1.5);
    border-radius: 20%;
  }
  75% {
    transform: rotate(270deg) scale(1.7);
    border-radius: 30%;
  }
  100% {
    transform: rotate(360deg) scale(2);
    border-radius: 50%;
  }
}

.handle-btn:hover {
  animation: rotate-box-changeShape 0.5s ease-in-out 1 forwards !important;
}

.subheading {
  z-index: 1;
  width: 100%;
  font-size: 3rem;
  font-family: sans-serif;
  color: white;
  text-align: center;
  margin: 5rem;
}

.navbar-toggler-icon {
  background-color: white;
  width: 30px !important;
  height: 30px !important;
  transition: 2s ease-in-out;
}

.navbar-nav .nav-link {
  text-align: center;
}
.navbar-nav {
  z-index: 2;
}

.navbar-expand-lg .navbar-nav {
  flex-direction: row;
  justify-content: space-around !important;
  width: 100%;
}

.bottom {
  width: 100%;
  height: 100rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottom div {
  width: 80%;
  height: 40%;
}

.project-section {
  width: 100%;
  padding: 2rem;
  background: linear-gradient(
    0deg,
    rgb(9, 115, 145) 100%,
    rgba(106, 167, 178, 1) 30%,
    rgb(9, 115, 145) 100%
  );
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.projectcard {
  font-size: 1.5rem;
  width: 40rem;
  height: auto;
  margin: 1rem;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
}

.projectimg {
  height: 30rem;
}

.backimg {
  width: 100%;
  height: fit-content;
  height: auto;
  position: relative;
  z-index: 2;
}
.floatingimg {
  position: absolute;
  z-index: 2;
  width: 30rem;
  height: auto;
  animation: float 10s linear infinite !important;
}

@keyframes float {
  0% {
    left: 100%;
  }
  10% {
    transform: scale(1);
    left: 90%;
  }
  30% {
    transform: scale(1.1);
    left: 70%;
  }
  100% {
    transform: scale(1);
    left: 0%;
  }
}

.bottomimg {
  position: absolute;
  bottom: 0px;
  z-index: 0;
}

.headerimg {
  z-index: 2;
  position: absolute;
  right: 10rem;
  top: 20rem;
  width: 20rem;
  height: auto;
}

.header2img {
  z-index: 2;
  position: absolute;
  left: 10rem;
  top: 20rem;
  width: 10rem;
  height: auto;
}
